import banner1 from "../../assets/home/cdbanner-1.webp";
import location from "../../assets/cdimages/location.svg";
import map from "../../assets/cdimages/mapp.webp";
import mapbg from "../../assets/cdimages/bgMob.png";
import cdstyle from "./style.css";
import CDStyleButton from "./styleButton";

const CDBanner = () => {
  return (
    <div class={cdstyle.bannerContent}>
      <div class={cdstyle.bannerContainer}>
        <div class={cdstyle.bannerContentContainer}>
          <img
            class={cdstyle.mapbg}
            src={map}
            alt='map'
            aria-hidden='true'
            rel='preload'
            fetchpriority='high'
          />
          <img
            class={cdstyle.mapbg_mobile}
            src={mapbg}
            alt='map'
            aria-hidden='true'
            rel='preload'
            fetchpriority='high'
          />
          <div class={cdstyle.bannerContentLeft}>
            <h2>
              Immigrate{" "}
              <span>
                t
                <img src={location} alt='location' />
              </span>{" "}
              Canada
            </h2>
            <p>Check your visa options with a representative today.</p>
            {/* <button class={cdstyle.cdbtn}>Check Your Eligibility</button> */}
            <CDStyleButton />
          </div>
          <div class={cdstyle.bannerContentRight}>
            <img
              src={banner1} // use normal <img> attributes as props
              alt='banner'
              width={717}
              height={521}
            />
          </div>
        </div>
      </div>
    </div>
  );
};
export default CDBanner;
